
body {
   margin: 0 auto;
   overflow-x: hidden;
   font-family: 'Fjalla One', sans-serif;
}

/* nav styles */

.navbar {
  z-index: 999;
  position: sticky;
  top: 0%;
  width: 100vw;
  padding: 10px;
  display: flex;
  align-items: center;
  background-color: rgba(0, 0, 0, 0);
  color: white;
}

.nav-bg {
  background-color: white;
}

.logo {
  width: 200px;
  height: auto;
  margin-right: 10px;
}

.nav-brand {
  width: 100%;
  cursor: pointer;
}

.nav-links {
  width: 100%;
  margin-left: 30%;
  display: flex;
  justify-content: space-around;
}

.nav-link {
  cursor: pointer;
}

/* hero image and banner text */

.hero-banner {
  margin-top: -8.75%;
  height: 100vh;
  width: 100vw;
  background: url('./assets/bats-banner-1.gif');
  background-size: cover;
  background-repeat: no-repeat;
}

.banner-bg {
  height: 100vh;
  background: rgb(5,5,55, .45);
}

.banner-text {
  position: relative;
  top: 30%;
  left: 25%;
  width: 50%;
  text-align: center;
  color: white;
  font-size: 2.5rem;
}

.subtitle {
  margin-top: -3%;
}

.banner-buttons {
  display: flex;
  justify-content: center;
}

.banner-button {
  background-color: white;
  color: black;
  font-size: 1.5rem;
  border: 1px white solid;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}

.banner-button:first-of-type {
  margin-right: 15px;
}

.banner-button:hover {
  background-color: black;
  color: white;
}

/* message */

.message {
  height: 50vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-self: center;
  padding-top: 10%;
  padding-bottom: 10%;
}

.sensei-img {
  width: 40%;
  align-self: center;
  text-align: center;
}

.sensei-img img {
  height: 300px;
  width: 300px;
  border-radius: 50%;
}

.sensei-msg {
  width: 50%;
  align-self: center;
  font-size: 1.25rem;
}

.sensei-msg a {
  text-decoration: none;
  color: black;
}

.flyer {
  width: 100%;
  align-self: center;
  border: 2px solid black;
  border-radius: 5px;
  padding: 10px 0px 10px 0px;
  text-align: center;
  cursor: pointer;
}

.flyer:hover {
  border: 2px solid white;
  background-color: black;
  color: white;
}

/* about */

.about {
  height: 70vh;
  width: 100vw;
  display: flex;
  flex-direction: row;
  align-self: center;
  align-items: center;
  justify-content: center;
  padding-top: 5%;
}

.about-img {
  width: 50%;
  height: 100%;
}

.about-img img {
  height: 100%;
  width: auto;
}

.about-info {
  width: 50%;
  height: 100%;
  background-color: #4A4843;
  color: white;
}

.about-cost {
  position: relative;
  left: 5%;
  width: 90%;
  display: flex;
  justify-content: space-between;
  text-align: center;
  padding: 0px 0px 0px 0px;
}

.hidden {
  visibility: hidden;
}

.fade-in-up {
  animation: fadeInUp .5s ease-in;
}

@keyframes fadeInUp {
    from {
        transform: translate3d(0,40px,0);
        opacity: 0
    }

    to {
        transform: translate3d(0,0,0);
        opacity: 1
    }
}

@-webkit-keyframes fadeInUp {
    from {
        transform: translate3d(0,40px,0);

    }

    to {
        transform: translate3d(0,0,0);
        opacity: 1
    }
}

.cost-info {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.about-styles {
  position: relative;
  width: 100%;
  display: flex;
  align-self: center;
  align-content: center;
  justify-content: space-around;
  margin-top: 10%;
}

.style-icon {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.5rem;
}

.style-icon img {
  width: 125px;
  height: 125px;
  margin-bottom: 15px;
  color: white;
}

/* register */

.register {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  text-align: center;
  padding-top: 5%;
  margin-bottom: -8%;
}

.register-select {
  display: flex;
}

.register-info {
  width: 50%;
  height: 90%;
  background-color: #C3B7BC;
  padding-top: 5%;
  padding-bottom: 3%;
}

.register-info:nth-of-type(2) {
  background-color: #B0A098;
}

.competitor, .volunteer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

button {
  font-family: 'Fjalla One', sans-serif;
  width: 50%;
  margin-top: 20px;
  background-color: white;
  color: black;
  font-size: 1.5rem;
  border: 1px white solid;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: black;
  color: white;
  cursor: not-allowed;
  -webkit-box-shadow: 0px 2px 16px 1px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 2px 16px 1px rgba(0,0,0,0.75);
  box-shadow: 0px 2px 16px 1px rgba(0,0,0,0.75);
}

label {
  display: block;
  font-family: 'Fjalla One', sans-serif;
  font-size: 1.5rem;
  margin-bottom: 3.5%;
}

input {
  position: relative;
  left: 25%;
  display: block;
  width: 50%;
  padding: 1% 0 1% 0;
  font-family: 'Fjalla One', sans-serif;
  font-size: 1.5rem;
  margin-bottom: 3.5%;
}

/* contact */

.contact {
  height: 70vh;
  width: 100vw;
}

.contact-header {
  text-align: center;
}

.contact-info {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-self: center;
  align-items: center;
  justify-content: center;
}

.contact-img {
  width: 50%;
  height: 100%;
}

.contact-img img {
  height: 100%;
}

.address-container {
  width: 50%;
  height: 100%;
  background-color: #4A4843;
  color: white;
}

.address {
  display: flex;
  flex-direction: column;
  text-align: center;
  position: relative;
  top: 15%;
}

.contact-social {
  margin-top: 20%;
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.contact-social img {
  height: 65px;
  width: auto;
}

.contact-footer {
  margin-top: 7%;
  display: flex;
  flex-direction: column;
  text-align: center;
  padding-bottom: 2%;
}

